
html{
  --primaryBackground:linear-gradient(
    60deg,
    hsla(0, 34%, 71%, 0.95),
    rgba(74, 76, 179, 0.95) 50%,
    hsla(0, 34%, 71%, 0.95)
  ) !important; 
}
html[data-theme="dark"]{
  --primaryBackground:linear-gradient(
    to bottom right,
    hsla(39, 100%, 63%, 0.95),
    rgba(255, 66, 161, 0.95) 50%,
    hsla(39, 100%, 54%, 0.5),
    rgba(255, 66, 161, 0.95),
    hsla(0, 6%, 15%, 0.95) 
  ) !important; 

}
